@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Righteous&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Anton&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App #titulo{font-family:'Bebas Neue', cursive;text-shadow:60px 20px #585858;margin-top:-4%}.App #quote-box{background-color:#fff;border-radius:10px;padding:25px;margin-left:25%;margin-right:25%;box-shadow:60px 16px #585858}.App #quote-box #text{font-family:'Anton', sans-serif}.App #quote-box #author{text-align:end;font-size:17px;font-family:'Anton', sans-serif}.App #quote-box #tweet-quote{float:left;padding-left:5%}.App #quote-box #new-quote{float:right;font-family:"Anton";font-size:5px;color:#fff;border-radius:10px;padding:7px}.App .App-header{min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}

